
<template>
<div>
<form v-if="have_results==='none' || have_results==='processing'"  @submit="checkForm">

  <div v-if="errors.length">
    <b>Please correct the following error(s):</b>
      <div 
  class="alert alert-danger" v-for="error in errors" :key="error">{{ error }}
  </div>
      
  </div>


    <div class="form-group">
    <label 
    for="exampleFormControlTextarea1"   
     >Data in CSV</label>
    <textarea class="form-control"  :disabled="have_results==='processing'"    v-model="csvdata"
    name="csvdata" id="csvdata" rows="25"></textarea>
  </div>

  <button type="submit"  class="btn btn-primary mb-2"><span v-if="have_results==='none'">Submit</span>
<div class="spinner-border" v-if="have_results==='processing'" role="status">
  <span class="sr-only">Loading...</span>
</div>    
     </button>
</form>

<div v-if="have_results==='finished'" class="container" >
<h1 style="color:Black;">Trend of the data</h1>
<img width="100%" v-bind:src="images['analysis']" alt="img"><hr>
<br>
<br>
<!--<img width="100%" v-bind:src="images['analysis_deep']" alt="img"><hr>!-->
<img width="100%" v-bind:src="images['analysis_validation']" alt="img"><hr>
<!--<img width="100%" v-bind:src="images['analysis_advance']" alt="img"><hr>!-->
<img width="100%" v-bind:src="images['final_predictions']" alt="img"><hr>

</div>
</div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'Graphs',
    data:function(){

    return {errors: [],
    csvdata: null,
    have_results:'none',
    images:null
     } 
  },
    methods:{
     checkForm: function (e) {
    e.preventDefault();


      this.errors = [];

      if (!this.csvdata) {
        this.errors.push('CSV Data required.');
      }
      
if (this.csvdata) {


this.csvdata.trim()
var csv=this.csvdata.trim().split('\n')
  
var header=csv[0]
var element_len=header.split(',').length;
if(element_len!==4){
    this.errors.push('CSV must have 4 columns')
    return;
    }


if(header.split(',')[0]!=='date' || header.split(',')[1]!=='store' ||
header.split(',')[2]!=='item' || header.split(',')[3]!=='sales'){
    this.errors.push('CSV  header names are not valid ')
    return;
    }


csv.shift();
var BreakException = {};

try {
csv.forEach(element => {  
  if(element.split(',').length!==element_len){
    this.errors.push('CSV data not valid. might be there is a ',' or element missing')

    throw BreakException;
    }
  if(!element.split(',')[0].match(/^\d{4}-(0?[1-9]|1[012])-(0?[1-9]|[12][0-9]|3[01])$/)){
    this.errors.push("CSV data not valid. date column must be like this '2017-01-12'")
    throw BreakException;
    }
});
} catch (e) {
  if (e !== BreakException) throw e;
  return
}

if(csv.length<50){
    this.errors.push('CSV not enough to predict the future sales')
    return;
    }
    this.have_results='processing';

axios
.post('/api/posting/',{'csv':this.csvdata})
.then(response => {
 let results=response['data']['results']
    this.have_results='finished';
    this.images=results
    //console.log(response.data);
  
  }).catch(function (error) {
   this.have_results='none';
    if (error.response) {
      // Request made and server responded
      console.log(error.response.data);
      console.log(error.response.status);
      console.log(error.response.headers);
    } else if (error.request) {
      // The request was made but no response was received
      console.log(error.request);
    } else {
      // Something happened in setting up the request that triggered an Error
      console.log('Error', error.message);
    }

  });



 
}
    }

  }
 }
</script>

